@import "variables.module";
//------colors-------------------
.violet{
    background: $d-violet;
}
.aqua{
    background: $d-aqua;
}
.green{
    background: $d-green;
}
.blue{
    background: $d-blue;
}
.red{
    background: $d-red;
}
//-----colors-border-------------
.violetBorder{
    border-color: $d-violet;
}
.aquaBorder{
    border-color: $d-aqua;
}
.greenBorder{
    border-color: $d-green;
}
.blueBorder{
    border-color: $d-blue;
}
.redBorder{
    border-color: $d-red;
}
//---------------------------
.bgColor{
    background: $d-bg-color;
}
.fontColor{
    color:$d-color;
}
.activeFontColor{
    color:$d-active-color;
}
.disabled{
    color:$d-disable-color;
    opacity: 0.5;
}
.bgElemColor{
    background: $d-bg-elem-color;
}
.bgElemDark{
    background: $d-bg-elem-dark;
}
.bgActive{
    background: $d-active-color;
}
.borderColor{
    border:3px solid $d-active-color !important;
}
.activeBorderColor{
    border:3px solid $d-active-dark-color !important;
}
.formBg{
    background: $d-form-color;
    caret: $d-color;
}

.fillColor{
    fill:$d-color;
}
.activeFillColor{
    fill:$d-active-color;
}
.strokeColor{
    stroke:$d-color;
}
.contentCheckbox{
    position: relative;
    z-index: 10;
    width: 20px;
    min-width: 20px;
    min-height: 20px;
    height: 20px;
    border: 1px solid;
    border-radius: $b-rad;

    &__checkbox {
        position: absolute;
        opacity: 0;
        width: 20px;
        height: 20px;
        z-index: 10;
        top: 0;
        left: 0;
    }
}
.hoverBgColor{
    &:hover{
        background: rgba($d-active-color,0.5);
    }
}